@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{
    font-family: 'Roboto', sans-serif;
}

body{
    margin: 0;
    padding: 0;
    background-color: #1C1F2A;
}
.menu-container>div:nth-child(1)>ul>li>span{
    margin-right: 10%;
}

.about-us_container{
    height: 85vh;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    color: #fff;
}

.cases-container>div>div:nth-child(2){
    display: flex;
}

.cases-container>div>div:nth-child(2)>div{
    margin: 2% 1%;
}

